.posts {
  padding: 0 6vw 3.625rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  padding-top: 200px;
}

@media (max-width: 1100px) {
  .posts {
      margin-top: 0;
      padding: 2rem 1.5rem 3.625rem;
  }
}

@media (max-width: 744px) {
  .posts {
      padding: 2rem 1rem 3rem;
  }
}